import React, { useEffect, useState } from "react";
import { SmithContract } from "../../../../../packages/neo/contracts/ftw/smith";
import { useWallet } from "../../../../../packages/provider";
import ContractCard from "./ContractCard";
import * as net from "net";
import DisplayNFT from "./DisplayNFT";
// import { RestAPI } from "../../../../../packages/neo/api";
// import { RUNE_PHASE_FILTER } from "../../../../../packages/neo/contracts/ftw/nft/consts";
// import { useWallet } from "../../../../../packages/provider";

interface ITokensProps {
  contractHash: string;
}
const Tokens = ({ contractHash }: ITokensProps) => {
  const [tokens, setTokens] = useState<string[]>([]);
  const [isLoading, setLoading] = useState(true);
  const [error, setError] = useState("");
  // const [propertiesModalActive, setPropertiesModalActive] = useState<string>();
  // 	setPropertiesModalActive(tokenId);
  // };
  const { network } = useWallet();
  useEffect(() => {
    async function fetchContractStatus() {
      setError("");
      setLoading(true);
      try {
        const res = await new SmithContract(network).getTokens(contractHash);
        setTokens(res);
      } catch (e: any) {
        setError(e.message);
      } finally {
        setLoading(false);
      }
    }
    fetchContractStatus();
  }, [contractHash, network]);
  console.log(tokens);
  return (
    <div>
      {isLoading ? (
        <div>Loading..</div>
      ) : error ? (
        <div>{error}</div>
      ) : (
        <div className="">
          {tokens.length > 0 ? (
            <div className="is-flex">
              {tokens.map((tokenId) => (
                <DisplayNFT
                  network={network}
                  contractHash={contractHash}
                  tokenId={tokenId}
                />
              ))}
            </div>
          ) : (
            <div></div>
          )}
        </div>
      )}
    </div>
  );
};

export default Tokens;
