import React, { useState } from "react";
import NFTListModal from "./NFTListModal";
import { useWallet } from "../../../../../../../packages/provider";
import { toast } from "react-hot-toast";
import { TournamentContract } from "../../../../../../../packages/neo/contracts/ftw/tournament";

interface IRegisterButtonProps {
  arenaNo: string;
  playerCount: number;
}
const RegisterButton = ({ arenaNo, playerCount }: IRegisterButtonProps) => {
  const [modalActive, setModalActive] = useState(false);
  const { connectedWallet, network, addPendingTransaction } = useWallet();
  const onPickModal = () => {
    if (connectedWallet) {
      setModalActive(true);
    } else {
      toast.error("Connect your wallet.");
    }
  };
  const onRegister = async (tokenId: string) => {
    if (connectedWallet) {
      try {
        const res = await new TournamentContract(network).register(
          connectedWallet,
          tokenId,
          arenaNo
        );
        addPendingTransaction(res);
        // onClose();
      } catch (e: any) {
        toast.error(e.message);
      }
    } else {
      toast.error("Please connect wallet.");
    }
  };
  return (
    <>
      <button
        disabled={playerCount === parseFloat(arenaNo)}
        className="button is-primary press-font"
        onClick={onPickModal}
      >
        Register ({playerCount}/{arenaNo})
      </button>
      {modalActive && connectedWallet && (
        <NFTListModal
          onRegister={onRegister}
          onClose={() => setModalActive(false)}
        />
      )}
    </>
  );
};

export default RegisterButton;
