import React, { useEffect, useState } from "react";
import Modal from "../../../../../../components/Modal";
import { NFTContract } from "../../../../../../../packages/neo/contracts";
import { useWallet } from "../../../../../../../packages/provider";
import PageLayout from "../../../../../../components/PageLayout";

interface INFTListModalModal {
  onClose: () => void;
  onRegister: (tokenId: string) => void;
}
const NFTListModal = ({ onRegister, onClose }: INFTListModalModal) => {
  const [tokens, setTokens] = useState<any>([]);
  const [isLoading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const { connectedWallet, network } = useWallet();
  useEffect(() => {
    async function fetchContractStatus() {
      setError("");
      setLoading(true);
      try {
        const res = await new NFTContract(network).getTokensOf(
          connectedWallet?.account.address
        );
        setTokens(res);
      } catch (e: any) {
        setError(e.message);
      } finally {
        setLoading(false);
      }
    }
    if (connectedWallet) {
      fetchContractStatus();
    }
  }, [connectedWallet, network]);
  return (
    <Modal onClose={onClose}>
      <>
        <div className="block">
          <h5 className="title is-5">Select a NFT to send to the ARENA</h5>
        </div>
        <div>
          {isLoading ? (
            <div>Loading..</div>
          ) : error ? (
            <div>{error}</div>
          ) : tokens.length > 0 ? (
            <div
              style={{
                flexFlow: "wrap",
              }}
              className="is-flex"
            >
              {tokens.map((token) => {
                return (
                  <figure
                    key={token.tokenId}
                    // style={{ width, height }}
                    className="image is-64x64"
                    onClick={() => onRegister(token.tokenId)}
                  >
                    <img src={token.image} />
                  </figure>
                );
              })}
            </div>
          ) : (
            <div>You don't have runes</div>
          )}
        </div>
      </>
    </Modal>
  );
};

export default NFTListModal;
