import React from "react";
import { IRuneMeta } from "../../../../../../../packages/neo/contracts/ftw/nft/interfaces";
import Modal from "../../../../../../components/Modal";
import { useWallet } from "../../../../../../../packages/provider";
import BetButton from "../../../../components/BetButton";
import LeaveButton from "../../../../components/LeaveButton";

interface IPlayerModalProps {
  arenaNo: string;
  player: IRuneMeta & { tokenId: string; gameOwner: string };
  onClose: () => void;
}
const PlayerModal = ({ arenaNo, player, onClose }: IPlayerModalProps) => {
  const { connectedWallet } = useWallet();
  return (
    <Modal onClose={onClose}>
      <div>
        <div className="columns">
          <div className="column">
            <img src={player.image} />
          </div>
          <div className="column">
            <div className="content">
              <strong>Name</strong>
              <br />
              <p>{player.name}</p>
              <strong>Phase</strong>
              <p>{player.phase}</p>
              <strong>Luck</strong>
              <p>{player.luck}</p>
            </div>
          </div>
        </div>
        <div className="columns">
          <div className="column">
            <BetButton arenaNo={arenaNo} tokenId={player.tokenId} />
          </div>
          <div className="column">
            <button onClick={onClose} className="button is-light is-fullwidth">
              Close
            </button>
          </div>
          {connectedWallet &&
          connectedWallet.account.address === player.gameOwner ? (
            <div className="column">
              <LeaveButton arenaNo={arenaNo} tokenId={player.tokenId} />
            </div>
          ) : (
            <></>
          )}
        </div>
      </div>
    </Modal>
  );
};

export default PlayerModal;
